@import "values";
.root{
  width: 100%;
}
.inactive {
  position: relative;
  width: 100%;
  height: 40px;
  margin-bottom: 8px;
  img{
    width: 100%;
    height: 40px;
  }
  span{
    @extend %DOption;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
